.portfolio {
	padding: 40px 0;
	color: $dark2;
	.container {
		max-width: 1460px; }
	.slider {
		background: #FFFFFF;
		box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.15);
		position: relative; }
	.block-title {
		text-align: center; }
	.slider {
		margin: 35px 0 0 0; }
	.twentytwenty-wrapper {
		width: 40%;
		height: 345px; }
	.slide {
		padding: 55px;
		&-top {
			width: 100%;
			@include flex(row, between, start, nowrap);
			&-left {
				width: 60%;
				padding: 0 20px 0 0; }
			&-right {
				width: 100%; } }
		&-bottom {
			width: 100%;
			@include flex(row, between, center, nowrap); }
		&__name {
			font: 500 32px/1 'PF', sans-serif;
			width: 100%;
			font-style: italic; }
		&__text {
			width: 100%;
			margin: 0 0 25px 0;
			font: 400 18px/1.6 'UniBook', sans-serif; }
		&__doc {
			font: 400 16px/1 'UniBook', sans-serif;
			span {
				display: inline-block;
				margin: 0 0 0;
				font: 500 20px/1 'PF', sans-serif; } } }

	.arrow {
		position: absolute;
		top: 50%;
		z-index: 2;
		@include mq($max-width: 1200px) {
			top: -40%; }
		&-prev {
			left: 0;
			transform: translate(-50%, -50%) rotate(180deg);
			@include mq($max-width: 700px); }
		&-next {
			right: 0;
			transform: translate(50%, -50%);
			@include mq($max-width: 700px); } } }


