.implantations-methods {
	padding: 50px 0;
	.block-title {
		text-align: center; }
	.container {
		max-width: 1530px; }
	.tabs {
		@include flex(row, null, null, wrap);
		background: white;
		width: 100%;
		margin: 50px 0 0 0;
		padding: 30px;
		border-radius: 30px;
		box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.15);
		&-list {
			width: 38%;
			background: #F3F3F3;
			&-item {
				width: 100%; }
			&__link {
				color: #5F5F5F;
				font-size: 27px;
				font-weight: 400;
				width: 100%;
				padding: 30px;
				&.active {
					background: $yellow;
					color: white; } } }
		&-card {
			padding: 0 0 0 30px;
			width: 62%;
			display: none;
			&.active {
				display: block; } } }
	.text-container {
		width: 100%;
		img {
			float: left;
			display: block;
			width: 316px;
			height: auto;
			margin: 0 30px 30px 0; }
		h3 {
			color: $yellow;
			font-size: 28px; }
		ul {
			column-count: 2;
			padding: 0 0 0 20px;
			li {
				list-style-type: disc; } } } }


