.vinir-types {
	padding: 50px 0;
	color: $dark;
	.container {
		max-width: 1730px; }
	.block-title {
		text-align: center; }
	.inner {
		box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.01);
		border-radius: 10px;
		background: white;
		padding: 20px 5px;
		margin: 30px 0 0 0;
		@include flex(row, null, null, wrap); }
	.item {
		border-radius: 10px;
		background: white;
		width: 31.3%;
		margin: 0 1%;
		padding: 20px;
		border: 1px solid #DCDCDC;
		&__title {
			margin: 25px 0;
			font: italic 700 23px/1 'PF', sans-serif;
			width: 100%; }
		&__text {
			width: 100%;
			font-size: 17px; }
		&-price {
			margin: 25px 0;
			width: 100%;
			@include flex(row, null, center, wrap);
			&__text {
				line-height: 1;
				font-size: 17px;
				font-weight: 700; }
			&__num {
				line-height: 1;
				font-size: 30px;
				font-weight: 700;
				color: $yellow;
				margin: 0 0 0 20px;
				span {
					font-weight: 400;
					font-size: 16px; } } }
		&-line {
			@include flex(row, between, center, wrap);
			width: 100%; }
		&__link {
			width: 60%;
			padding: 25px 5px; }
		&__infolink {
			color: $dark2;
			font: italic 700 20px/1 'PF', sans-serif;
			text-decoration: underline;
			text-align: center;
			width: 40%;
			@include mq($min-width: 1200px) {
				&:hover {
					text-decoration: none; } } } } }

