.after-operation {
	padding: 45px 0;
	.container {
		max-width: 1510px;
		@include mq($max-width: 1550px) {
			max-width: 1170px; } }
	.item {
		margin: 35px 0 0 0;
		@include flex(row, between, start, wrap);
		&-img {
			width: 49%;
			@include mq($max-width: 1200px) {
				width: 100%; } }
		&__img {
			display: block;
			width: 100%; } }
	.text-container {
		width: 49%;
		@include mq($max-width: 1200px) {
			width: 100%; } } }
