.stages {
	padding: 15px 0 30px;
	text-align: center;
	color: $dark;
	.container {
		max-width: 1600px; }
	.inner {
		margin: 35px 0 0 0;
		@include flex(row, null, null, wrap); }
	.item {
		box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.06);
		border-radius: 10px;
		text-align: left;
		position: relative;
		width: 48%;
		margin: 1%;
		padding: 56px 40px;
		&:nth-child(1) {
			@include bg(null, white, 'stages/bg1.jpg', no-repeat, center, center, contain); }
		&:nth-child(2) {
			@include bg(null, white, 'stages/bg2.jpg', no-repeat, center, center, contain); }
		&:nth-child(3) {
			@include bg(null, white, 'stages/bg3.jpg', no-repeat, center, center, contain); }
		&:nth-child(4) {
			@include bg(null, white, 'stages/bg4.jpg', no-repeat, center, center, contain); }
		&::before {
			content: '';
			border-radius: 10px;
			left: 20px;
			right: 20px;
			top: 20px;
			bottom: 20px;
			border: 1px solid #CCCCCC;
			position: absolute; }
		&__title {
			font: 500 25px/1 'PF', sans-serif;
			font-style: italic;
			span {
				color: $brown; } }
		&__text {
			margin: 26px 0 0 0;
			font: 18px/1.6 'UniBook', sans-serif; } } }
