.similar-articles {
	padding: 35px 0 50px 0;
	.container {
		max-width: 1510px;
		padding: 0 15px;
		@include mq($max-width: 1700px) {
			max-width: 1370px; }
		@include mq($max-width: 1550px) {
			max-width: 1170px; } }
	.slider {
		width: calc(100% + 30px);
		margin: 35px 0 0 -15px; }
	.slide {
		color: black;
		text-align: center;
		margin: 0 15px;
		&:hover {
			color: $brown;
			.item__img {
				opacity: .8; } }
		&__img {
			display: block;
			width: 100%;
			@include trs; }
		&__title {
			margin: 20px 0 0 0;
			font-size: 24px;
			@include mq($max-width: 1550px) {
				font-size: 18px; } }
		&__text {
			font-family: 'UniBook', sans-serif;
			font-size: 18px;
			@include mq($max-width: 1550px) {
				font-size: 14px; } } }
	.arrows {
		@include flex(row, end, center, wrap);
		margin: 35px 0; }
	.arrow {
		position: absolute;
		top: 50%;
		transform: translate(0, -100%);
		z-index: 3;
		&-prev {
			left: -60px;
			transform: translate(0, -100%) rotate(180deg);
			margin: 0 20px 0 0;
			@include mq($max-width: 1300px) {
				left: 0; } }
		&-next {
			right: -60px;
			@include mq($max-width: 1300px) {
				right: 0; } } } }
