.breadcrumbs {
	padding: 15px 0;
	.container {
		max-width: 1450px; }
	.list {
		&:before {
			content: '';
			display: inline-block;
			vertical-align: top;
			width: 24px;
			height: 24px;
			margin: 0 11px 0 0;
			@include bg(null, null, 'icons/home.svg', no-repeat, center, center, contain); }
		li {
			display: inline-block;
			&:not(:last-child) {
				&::after {
					content: '';
					display: inline-block;
					width: 10px;
					height: 10px;
					margin: 0 15px;
					transform: rotate(-90deg);
					@include bg(null, null, 'icons/arrow-down.svg', no-repeat, center, center, contain); } } }

		a {
			line-height: 1;
			color: $dark;
			font-family: 'UniBook', sans-serif;
			&:hover {
				color: $brown; } } } }
