// main page elements
html {
	overflow-x: hidden;
	font-family: 'Uni', sans-serif; }
body {
	font-size: 18px;
	color: black;
	font-family: 'Uni', sans-serif;
	font-weight: 400;
	background: $body-bg;
	overflow-x: hidden;
	@include mq($max-width: 1600px) {
		font-size: 16px; }
	@include mq($max-width: 1200px) {
		font-size: 14px; }
 }	// +scrollbar(10px, auto, $yellow, black, black, null)

.page-container {
	max-width: 1920px;
	width: 100%;
	margin: 0 auto;
	position: relative;
	.circle1 {
		display: block;
		position: absolute;
		width: 545px;
		height: 545px;
		border-radius: 100%;
		border: 74px solid #FCFCFC;
		right: 0;
		top: 20%;
		transform: translate(50%, 0);
		z-index: -1; }
	.circle2 {
		position: absolute;
		transform: translate(50%, 0);
		z-index: -1;
		bottom: 20%;
		right: 0;
		background: #FCFCFC;
		box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.15);
		width: 1000px;
		height: 1000px;
		border-radius: 100%;
		&::before {
			content: '';
			display: block;
			border-radius: 100%;
			width: 150%;
			height: 150%;
			z-index: -1;
			top: 50%;
			left: 50%;
			position: absolute;
			transform: translate(-50%, -50%);
			background: rgba(253, 253, 253, 0.2);
			box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.15); } } }

.container {
	max-width: 1170px;
	width: 100%;
	padding: 0 15px;
	margin: 0 auto;
	@include mq($max-width: 1200px) {
		padding: 0 15px; }
	@include mq($max-width: 575px) {
		padding: 0 10px; } }

.no-page {
	margin: 150px 0;
	.action {
		max-width: 350px; } }

.btn-callback {
	font-weight: 700;
	border-radius: 43px;
	padding: 12px 32px;
	color: white;
	@include flex(row, null, center, nowrap);
	background: $btn-callback-gradient;
	cursor: pointer;
	@include trs;
	&:hover {
		background: black; }
	svg {
		margin: 0 12px 0 0;
		path {
			fill: white; } } }

.btn-entry {
	font-weight: 700;
	border-radius: 43px;
	padding: 12px 32px;
	color: white;
	@include flex(row, null, center, nowrap);
	background: $btn-entry-gradient;
	cursor: pointer;
	@include trs;
	&:hover {
		background: black; }
	svg {
		margin: 0 12px 0 0;
		path {
			fill: white; } } }

.submit {
	text-align: center;
	color: white;
	background: linear-gradient(267.89deg, #CD9B1C 0%, #E6B433 99.45%);
	padding: 22.5px 55px;
	font-weight: 700;
	border-radius: 5px;
	font-size: 21px;
	cursor: pointer;
	@include trs;
	@include mq($max-width: 1200px) {
		padding: 11px 27px; }
	@include mq($max-width: 575px) {
		font-size: 18px; }
	&:hover {
		background: linear-gradient(267.89deg, #CD9B1C 99.45%, #E6B433 0%); } }

.block-title {
	font-weight: 300;
	font-size: 35px;
	color: black;
	line-height: 1;
	@include mq($max-width: 800px) {
		font-size: 30px; }
	@include mq($max-width: 575px) {
		font-size: 24px; }
	span {
		font-size: 38px;
		font-style: italic;
		font-family: 'PF', sans-serif;
		@include mq($max-width: 800px) {
			font-size: 32px; }
		@include mq($max-width: 575px) {
			font-size: 24px; } }
	&.type2 {
		span {
			font-style: normal;
			font-family: inherit;
			color: $yellow2;
			font-weight: 400; } } }

.block-text {
	font-family: 'UniBook', sans-serif;
	line-height: 1.7; }

.logo {
	@include flex(row, null, center, wrap);
	color: black;
	@include mq($max-width: 700px) {
		flex-direction: column;
		align-items: flex-start; }
	&:hover {
		.logo__img {
			opacity: .5; } }
	&__img {
		display: block;
		max-width: 141px;
		width: 100%;
		@include trs;
		@include mq($max-width: 1300px) {
			max-width: 121px; } }
	&__title {
		font-size: 24px;
		line-height: 1;
		text-transform: uppercase; }
	&__text {
		font-size: 14px;
		padding: 0 0 0 16px;
		margin: 0 0 0 16px;
		border-left: 1px solid #000;
		@include mq($max-width: 1300px) {
			margin: 0 0 0 5px;
			padding: 0 0 0 5px;
			font-size: 12px; }
		@include mq($max-width: 700px) {
			margin: 0;
			padding: 5px 0 0 0;
			border: none; } } }

.entry-form {
	@include mq($max-width: 575px) {
		width: 100%; }
	&.horizontal {
		@include flex(row, null, null, wrap);
		max-width: 533px;
		width: 100%;
		@include mq($max-width: 1500px) {
			max-width: 507px; }
		@include mq($max-width: 1200px) {
			max-width: 450px; }
		.entry-form__btn {
			position: relative;
			z-index: 2;
			margin-left: -10px;
			@include mq($max-width: 575px) {
				margin: 0;
				width: 100%; } }
		@include mq($max-width: 575px) {
			flex-direction: column; } }
	&.vertical {
		width: 282px;
		@include flex(column, null, null, nowrap);
		.entry-form-label {
			margin: 0 0 12px 0;
			width: 100%; }
		.entry-form__input {
			width: 100%; }
		.entry-form__btn {
			width: 100%;
			margin: 0; } }
	&-label {
		position: relative;
		width: 100%;
		@include mq($max-width: 575px) {
			width: 100%;
			margin: 0 0 10px 0; }
		&.big {
			width: 100%;
			margin: 0 0 10px 0;
			.entry-form__input {
				text-align: center;
				width: 100%; } }
		svg {
			display: block;
			width: 23px;
			position: absolute;
			top: 50%;
			left: 25px;
			z-index: 2;
			transform: translate(0, -50%);
			@include mq($max-width: 1200px) {
				left: 15px; }
			path {
				fill: $brown; } } }
	&-row {
		@include flex(row, null, null, nowrap);
		@include mq($max-width: 575px) {
			flex-wrap: wrap; } }
	&__input {
		background: white;
		padding: 19.5px 72px;
		border-radius: 6px;
		border: 3px solid $brown;
		width: 311px;
		font-family: 'UniBook', sans-serif;
		color: $brown;
		font-size: 18px;
		@include trs;
		&:hover,
		&:focus {
			border-color: $dark; }
		@include mq($max-width: 1450px) {
			width: 285px; }
		@include mq($max-width: 1200px) {
			padding: 10px 50px; }
		@include mq($max-width: 575px) {
			font-size: 15px;
			width: 100%;
			text-align: center; }
		&::placeholder {
			font-family: 'UniBook', sans-serif;
			font-size: 18px;
			color: $brown;
			@include mq($max-width: 575px) {
				font-size: 15px; } } } }

.arrow {
	background: $gray;
	border-radius: 100%;
	width: 60px;
	height: 60px;
	@include flex(row, center, center, nowrap);
	position: absolute;
	top: 50%;
	cursor: pointer;
	@include trs;
	&:hover {
		background: $btn-callback-gradient;
		svg {
			width: 24px; } }
	svg {
		@include trs;
		display: block;
		width: 16px;
		path {
			fill: white; } } }

