.prices-spoilers {
	padding: 30px 0;
	.container {
		max-width: 1390px; }
	&-block {
		margin: 0 0 70px 0;
		@include mq($max-width: 1550px) {
			margin: 0 0 35px 0; } }
	&-item {
		margin: 0 0 50px 0;
		@include mq($max-width: 1550px) {
			margin: 0 0 20px 0; }
		&.active {
			.prices-spoilers-item__arrow {
				transform: rotate(180deg) translate(0, 50%); } }
		&-row {
			background: #FDFDFD;
			border-radius: 80px;
			box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.19);
			text-align: center;
			padding: 24px 80px;
			position: relative;
			cursor: pointer;
			min-height: 80px;
			@include flex(row, center, center, nowrap);
			@include mq($max-width: 800px) {
				min-height: 50px;
				padding: 18px 60px; }
			@include mq($max-width: 575px) {
				padding: 18px 60px 18px 20px; }
			&:hover {
				.prices-spoilers-item__arrow {
					background: $brown; } } }
		&__title {
			text-align: center;
			font-size: 29px;
			line-height: 1;
			width: 100%;
			@include mq($max-width: 1550px) {
				font-size: 22px; }
			@include mq($max-width: 800px) {
				font-size: 18px; }
			@include mq($max-width: 575px) {
				font-size: 15px;
				text-align: left; } }
		&__arrow {
			cursor: pointer;
			width: 67px;
			height: 67px;
			border-radius: 67px;
			position: absolute;
			right: 5px;
			top: 50%;
			transform: translate(0, -50%);
			background: #959595;
			@include trs;
			@include flex(row, center, center, nowrap);
			@include mq($max-width: 800px) {
				width: 40px;
				height: 40px; }
			svg {
				display: block;
				width: 24px;
				stroke: white;
				@include mq($max-width: 800px) {
					width: 16px; } } }
		&__list {
			margin: 40px 0 0 0;
			@include mq($max-width: 1550px) {
				margin: 20px 0 0 0; }
			li {
				padding: 15px 0 15px 15px;
				@include flex(row, between, null, nowrap);
				border-bottom: 1px solid #D4D4D4;
				span {
					font-size: 24px;
					width: 80%;
					font-family: 'UniBook', sans-serif;
					@include mq($max-width: 1550px) {
						font-size: 20px; }
					@include mq($max-width: 800px) {
						font-size: 16px; }
					@include mq($max-width: 575px) {
						font-size: 14px; }
					&.see-info {
						padding: 0 10px 0 0;
						&:after {
							content: '';
							vertical-align: middle;
							display: inline-block;
							width: 20px;
							height: 20px;
							margin: 0 10px;
							@include bg(null, null, 'icons/info-icon.svg', no-repeat, center, center, contain); } } }
				b {
					text-align: right;
					width: 20%;
					font-size: 19px;
					@include mq($max-width: 800px) {
						font-size: 17px; }
					@include mq($max-width: 575px) {
						font-size: 15px; } } } } }
	.action {
		text-align: center;
		@include mq($max-width: 575px) {
			@include flex(column, null, center, nowrap); }
		li {
			display: inline-block;
			margin: 0 30px;
			@include mq($max-width: 575px) {
				margin: 15px 0; } } }

	.infolink {
		text-decoration: underline;
		color: black;
		position: relative;
		font-size: 17px;
		font-family: 'UniBook', sans-serif;
		&:hover {
			color: $brown;
			&:before {
				filter: grayscale(0); } }
		&::before {
			content: '';
			vertical-align: middle;
			display: inline-block;
			width: 20px;
			height: 20px;
			margin: 0 10px;
			position: absolute;
			top: 3px;
			left: -40px;
			filter: grayscale(1);
			@include bg(null, null, 'icons/info-icon.svg', no-repeat, center, center, contain); } } }
