.included {
	padding: 100px 0;
	text-align: center;
	color: $dark;
	@include bg(null, null, 'included/tape.png', no-repeat, center, center, contain);
	.container {
		max-width: 1390px; }
	.inner {
		margin: 30px 0 0 0;
		@include flex(row, null, null, wrap); }
	.item {
		background: white;
		box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.06);
		border-radius: 10px;
		text-align: left;
		position: relative;
		width: 31.3%;
		margin: 1%;
		padding: 56px 40px;
		&::before {
			content: '';
			border-radius: 10px;
			left: 20px;
			right: 20px;
			top: 20px;
			bottom: 20px;
			border: 1px solid #CCCCCC;
			position: absolute; }
		&__title {
			font-family: 'PF', serif;
			padding: 0 0 0 20px;
			font-weight: 500;
			font-style: italic;
			font-size: 25px; }
		&__text {
			margin: 20px 0 0 0;
			font: 18px/1.6 'UniBook', sans-serif; } } }
