.reviews {
	padding: 30px 0;
	.container {
		max-width: 1600px;
		padding: 0 15px;
		@include flex(row, between, null, wrap); }
	.item {
		width: 48%;
		padding: 50px;
		border-radius: 8px;
		margin: 15px 1%;
		background: white;
		border: 1px solid #E9E9E9;
		@include mq($max-width: 1200px) {
			padding: 25px; }
		@include mq($max-width: 700px) {
			width: 100%;
			margin: 10px 0; }
		&-top {
			@include flex(row, start, start, wrap);
			margin: 0 0 35px 0;
			@include mq($max-width: 1000px) {
				margin: 0 0 10px 0; }
			&-inner {
				margin: 0 0 0 45px;
				@include mq($max-width: 1200px) {
					margin: 15px 0;
					width: 100%; } } }
		&-line {
			@include flex(row, between, end, wrap); }
		&-img {
			position: relative;
			box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.11);
			border-radius: 100%;
			width: 140px;
			height: 140px;
			&:before {
				content: '';
				border: 1px solid #E8E8E8;
				width: 175px;
				height: 175px;
				position: absolute;
				top: 50%;
				left: 50%;
				border-radius: 100%;
				transform: translate(-50%, -50%);
				@include mq($max-width: 1200px) {
					display: none; } } }
		&__img {
			display: block;
			border-radius: 100%; }
		&__name {
			font-weight: 700;
			font-size: 19px;
			color: $dark2;
			@include mq($max-width: 575px) {
				font-size: 16px; } }
		&__operation {
			display: inline-block;
			margin: 10px 0 20px 0;
			line-height: 1.8;
			padding: 0 35px;
			font-weight: 300;
			color: white;
			background: $yellow;
			border-radius: 25px;
			font-size: 14px; }
		&__doc {
			font-size: 17px;
			@include mq($max-width: 575px) {
				font-size: 16px; }
			span {
				font-weight: 500; } }
		&__text {
			line-height: 1.6;
			font-family: 'UniBook', sans-serif;
			font-size: 16px;
			@include mq($max-width: 575px) {
				font-size: 14px; } }
		&__date {
			margin: 30px 0 0 0;
			font-weight: 300;
			font-size: 15px;
			@include mq($max-width: 575px) {
				font-size: 12px; } }
		&__videolink {
			font-weight: 300;
			font-size: 15px;
			color: black;
			@include mq($max-width: 575px) {
				font-size: 14px; }
			&:hover {
				color: $brown;
				&:after {
					width: 0; } }
			&:after {
				content: '';
				display: block;
				width: 100%;
				height: 1px;
				@include trs;
				background: #000; } } } }

