.articles {
	padding: 60px 0 80px 0;
	@include mq($max-width: 1550px) {
		padding: 40px 0; }
	.container {
		max-width: 1480px;
		width: 100%;
		margin: 0 auto; }
	.inner {
		@include flex(row, null, null, wrap); }
	.item {
		width: 31.3%;
		margin: 2% 1%;
		color: black;
		text-align: center;
		@include mq($max-width: 1200px) {
			width: 48%; }
		@include mq($max-width: 575px) {
			width: 100%;
			margin: 2% 0; }
		&:hover {
			color: $brown;
			.item__img {
				opacity: .8; } }
		&__img {
			display: block;
			width: 100%;
			@include trs; }
		&__title {
			margin: 20px 0 0 0;
			font-size: 24px;
			@include mq($max-width: 1550px) {
				margin: 15px 0 0 0;
				font-size: 20px; }
			@include mq($max-width: 1200px) {
				font-size: 18px; } }
		&__text {
			font-family: 'UniBook', sans-serif;
			font-size: 18px;
			@include mq($max-width: 1550px) {
				font-size: 14px; } } }
	.action {
		margin: 45px 0 0 0;
		text-align: center;
		width: 100%; } }

