.book-text {
	padding: 150px 0;
	position: relative;
	.container {
		max-width: 1530px; }
	&__img {
		position: absolute;
		left: 0;
		top: 50%;
		transform: translate(0, -50%);
		width: 60%;
		z-index: -1; }
	.block-title {
		margin: 0 0 60px 0; }
	.inner {
		max-width: 620px;
		width: 100%;
		margin: 0 0 0 auto; }
	.action {
		margin: 60px 0 0 0; } }



