.category-about {
	padding: 50px 0 35px;
	.container {
		max-width: 1430px;
		background: #FFFFFF;
		box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.15);
		border-radius: 10px;
		padding: 0;
		@include flex(row, between, center, wrap); }
	&-left {
		width: 45%;
		overflow: hidden;
		border-radius: 10px 0 0 10px; }
	&-right {
		padding: 20px;
		width: 52%; }
	&__img {
		display: block;
		width: 100%;
		height: 100%;
		object-fit: cover; }
	.text-container {
		margin: 30px 0 0 0;
		height: 280px;
		overflow: auto; } }



